.circle {
  background: teal;
  border-radius: 50%;
  width: 500px;
  height: 500px;
  margin: 0;

  position: absolute;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%);
}

.centered-text {
  margin: 0;

  color: white;

  position: absolute;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%);
}
