.report__control-panel {
    display: flex;
    justify-content: space-evenly
}

.report__control-panel label {
    margin-right: 10px
}

.report__control-panel input, .report__control-panel select {
    border: 1px solid #C2C2C2;
    border-radius: 3px;
    box-sizing: border-box;
    box-shadow: 1px 1px 4px #EBEBEB;
    /*height: 29px;*/
    padding: 7px;
    outline: none;
    width: 100px
}

.report__control-panel button {
    background: #FF8500;
    border: none;
    border-radius: 3px;
    box-shadow: 1px 1px 4px #EBEBEB;
    color: #fff;
    padding: 8px 15px 8px 15px;
    height: 29px;
    width: 120px
}

.report__control-panel button:disabled {
    background: #C2C2C2;
    color: #EBEBEB
}

.report__preview-container {
    padding-right: 40px;
}

.report__preview-container .page {
    background-color: #fff;
    box-shadow: 8px 8px 5px #C2C2C2;
    margin-top: 20px;
    outline: 1px solid #EBEBEB;
    padding: 5% 1%
}

.report__preview-container .page.a4 {
    height: 297mm;
    width: 210mm
}

.report__preview-container .page.a4.landscape {
    height: 210mm;
    width: 100%;
}

.report__preview-container .page.letter {
    height: 279.4mm;
    width: 215.9mm
}

.report__preview-container .page.letter.landscape {
    height: 215.9mm;
    width: 279.4mm
}

.report__preview-container .page.legal {
    height: calc(356mm * 0.9);
    width: calc(216mm * 0.9)
}

.report__preview-container .page.legal.landscape {
    height: calc(216mm * 0.9);
    width: calc(356mm * 0.9)
}

.report__preview-container .page__table {
    display: table;
    text-align: left;
    width: 100%
}

.report__preview-container .page__table .header {
    background-color: #EBEBEB;
    display: table-header-group
}

.report__preview-container .page__table .header .col {
    font-weight: bold;
    text-transform: uppercase
}

.report__preview-container .page__table .row {
    display: table-row
}

.report__preview-container .page__table .row:nth-child(odd) {
    background-color: #EBEBEB
}

.report__preview-container .page__table .col {
    display: table-cell;
    font-size: 12px;
    padding: 2px 5px;
    width: auto
}

